import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    components: {
      'top': require('../components/main/MainTop.vue').default,
      'bottom': require('../components/main/MainBottom.vue').default
    }
  }, {
    path: '/about',
    components: {
      'top': require('../components/about/AboutTop.vue').default,
      'bottom': require('../components/about/AboutBottom.vue').default
    }
  }, {
    path: '/project',
    name: 'project',
    components: {
      'top': require('../components/project/ProjectTop.vue').default,
      'bottom': require('../components/project/ProjectBottom.vue').default
    }
  }, {
    path: '*',
    redirect: '/'
  }]
})



